import Vue from 'vue'
import VueRouter from 'vue-router'
import TitleCard from '../views/TitleCard.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'TitleCard',
    component: TitleCard
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
