import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    message: {
      subject: ' ',
      messageText: '',
    },
    link: 'mailto:shuric03@gmail.com?subject=&body=',
    isSend: false,
  },
  mutations: {
    saveMessage(state, messageText){
      state.link = "mailto:shuric03@gmail.com?subject=&body=" + messageText.messageText
      console.log('state ', state.message)
      state.message = messageText
      setTimeout(() => state.isSend = true, 1)
      
     
    }
  },
  actions: {
    saveMessage(context, messageText){
      context.commit('saveMessage', messageText)
    }
  },
  modules: {
  }
})
